<template>
  <tbody>
    <tr class="text-center" v-for="(sec, index) in secList" :key="sec.id">
      <!-- first row -->
      <td class="px-1" v-if="index === 0">
        {{ getSubCode(MaxYearsList[0].value) }}
      </td>
      <td class="px-1" v-if="index === 0">
        {{ getSubCode(MaxYearsList[1].value) }}
      </td>
      <td class="px-1" v-if="index === 0">
        {{ getSubCode(MaxYearsList[2].value) }}
      </td>
      <td class="px-1" v-if="index === 0">
        {{ getSubCode(MaxYearsList[3].value) }}
      </td>

      <!-- second row -->
      <td class="px-1" colspan="4" v-if="index === 1">
        {{ LastSubject.name }}
      </td>

      <td class="px-1" colspan="4" v-if="index > 1"></td>

      <td class="px-1">{{ sec.secId }}</td>
      <td class="px-1">{{ sec.group }}</td>
      <!-- round 1 -->
      <td class="px-1">{{ sec.quota }}</td>
      <td class="px-1">0</td>
      <td class="px-1">0</td>
      <!-- round 2 -->
      <td class="px-1">0</td>
      <td class="px-1">0</td>
      <td class="px-1">0</td>
      <!-- round 3 -->
      <td class="px-1">0</td>
      <td class="px-1">0</td>
      <td class="px-1">0</td>

      <td class="px-1">0</td>
      <td class="px-1 ">
        <div class="inline overflow-hidden whitespace-nowrap">
          <div v-if="getInstName(sec.instId).fullName.length < 16">
            {{ getInstName(sec.instId).fullName }}
          </div>
          <div v-else>
            {{ getInstName(sec.instId).fullName.substring(0, 16) + ".." }}
          </div>
        </div>
      </td>
      <td class="px-1">
        ปิดบรรยาย
      </td>
      <td class="px-1">
        2
      </td>
      <td class="px-1">
        56
      </td>
      <td>
        <base-button>แก้ไข</base-button>
      </td>
    </tr>
    <tr v-html="secondRow"></tr>

    <tr>
      <td colspan="21">
        <hr />
      </td>
    </tr>
  </tbody>
</template>

<script>
// import SecItem from "./SecItem.vue";
export default {
  components: {},
  props: {
    offLink: {
      required: true,
    },
    yearNo: {
      required: true,
    },
  },
  computed: {
    secondRow() {
      if (this.secList.length === 1) {
        return (
          '<td class="px-1 text-center" colspan="4" v-if="index === 1">' +
          this.LastSubject.name +
          "</td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>" +
          "<td></td>"
        );
      } else {
        return "";
      }
    },
    secList() {
      return this.$store.getters["quota/getQuotaByOffLink"](this.offLink);
    },
    subjectsList() {
      return this.$store.getters["subjects/getSubjectsByOffLink"](this.offLink);
    },
    LastSubject() {
      return this.$store.getters["subjects/getLastSubjectByOffLink"](
        this.offLink
      );
    },
    MaxYearsList() {
      return this.$store.getters["subjects/getMaxYears"](this.yearNo);
    },
  },
  methods: {
    getInstName(instId) {
      return this.$store.getters["instructors/getNameById"](instId);
    },
    getSubCode(year) {
      const sub = this.$store.getters["subjects/getSubjectByYearAndOffLink"]({
        year: year,
        offLink: this.offLink,
      });
      if (sub) {
        return sub.code;
      } else {
        return "-";
      }
    },
  },
};
</script>

<style scoped>
td {
  word-wrap: break-word;
}
</style>
