import { getId } from "../../../js/utility.js";
export default {
  async addQuota(context, payload) {
    const quotaData = {
      subId: payload.subId,
      secId: payload.secId,
      offLink: payload.offLink,
      group: payload.group,
      campus: payload.campus,
      quota: payload.quota,
      instId: payload.instId,
      updateBy: context.rootGetters.userId,
      updateDate: new Date().toLocaleString(),
    };

    // Update data in Firebase

    // const token = context.rootGetters.token;

    console.log("start sending data to firbase");

    const response = await fetch(`${process.env.VUE_APP_DB_URL}/quota.json`, {
      method: "POST",
      body: JSON.stringify(quotaData),
    });

    // const responseData = await response.json();

    if (!response.ok) {
      alert("can't send data to server,please try agian later");
    }

    // Update data in Vuex
    context.commit("addQuota", {
      ...quotaData,
      id: getId(),
    });
  },
  async setQuota(context) {
    // const token = context.rootGetters.token;
    console.log("get Quota data from Firebase");
    const response = await fetch(`${process.env.VUE_APP_DB_URL}/quota.json`);
    const responseData = await response.json();
    if (!response.ok || responseData == null) {
      const error = new Error(
        response.message || "Can't receive Data from server "
      );
      console.log(error);
      throw error;
    }

    console.log(responseData);

    let quota = [];
    for (const key in responseData) {
      const newQuota = {
        id: key,
        subId: responseData[key].subId,
        secId: responseData[key].secId,
        offLink: responseData[key].offLink,
        group: responseData[key].group,
        campus: responseData[key].campus,
        quota: responseData[key].quota,
        instId: responseData[key].instId,
        updateBy: responseData[key].updateBy,
        updateDate: responseData[key].updateDate,
      };
      quota.push(newQuota);
    }
    console.log(quota);
    context.commit("setQuota", quota);
  },
};
