<template>
  <div class="px-2">
    <base-card class="text-center">
      <h1 class="py-4">รายวิชา</h1>
      <!-- <input
        class="border-2 border-gray-500 rounded-lg"
        type="text"
        placeholder="ค้นหาข้อมูล มีหน้าที่อะไร"
      /> -->
    </base-card>

    <base-card>
      <h3 class="text-center ">เมนู</h3>

      <div class="flex justify-center py-4 ">
        <base-button
          @click="
            showForm !== 'addCur' ? (showForm = 'addCur') : (showForm = null)
          "
          >เพิ่มหลักสูตร</base-button
        >
        <base-button
          @click="
            showForm !== 'deleteCur'
              ? (showForm = 'deleteCur')
              : (showForm = null)
          "
          >ลบหลักสูตร</base-button
        >
        <base-button @click="addSubject">เพิ่มวิชา</base-button>

        <base-button @click="saveSubject" class="bg-primary-300 px-8"
          >บันทึกข้อมูล</base-button
        >

        <!-- <base-button @click="deleteSubjects">ลบวิชา</base-button> -->
        <!-- <base-button @click="null">ลบหลักสูตร</base-button> -->
      </div>
      <div>
        <h4 class=" text-red-500 text-center" v-if="saveSubjectDialog">
          คุณกรอกรายละเอียดของบางวิชาไม่ครบ โปรดตรวจสอบและลองใหม่อีกครั้ง
        </h4>
      </div>
      <div v-if="showForm === 'addCur'" class="flex justify-center">
        <div class="max-w-lg ">
          <h5 class="text-center pt-4">เพิ่มหลักสูตร</h5>
          <form @submit.prevent="addCurriculum()">
            <label>ปีการศึกษา</label>
            <input
              type="text"
              class="form-control"
              v-model.trim="inputCopyToCur"
            />
            <label>คัดลอกจากปี</label>
            <select class="form-control" v-model="selectedCopyFormCur">
              <option
                :value="year.value"
                v-for="year in years"
                :key="year.value"
                >{{ year.text }}</option
              >
            </select>
            <div class="flex justify-center">
              <base-button mode="outline px-10 mt-2">เพิ่ม</base-button>
            </div>
          </form>
        </div>
      </div>

      <div v-if="showForm === 'deleteCur'" class="flex justify-center">
        <div class="max-w-lg ">
          <h5 class="text-center pt-4">ลบหลักสูตร</h5>
          <form @submit.prevent="deleteCurriculum()">
            <label>ปีการศึกษา</label>
            <select class="form-control" v-model="selectedDeleteCur">
              <option
                :value="year.value"
                v-for="year in years"
                :key="year.value"
                >{{ year.text }}</option
              >
            </select>
            <div class="flex justify-center">
              <base-button mode="outline px-10 mt-2">ลบ</base-button>
            </div>
          </form>
        </div>
      </div>
    </base-card>

    <!-- Pagination -->
    <div class="flex justify-end items-center py-4">
      <h5>ปีการศึกษา</h5>
      <div class="flex justify-center">
        <div class="pr-2" v-for="year in years" :key="year">
          <base-button
            mode="gray"
            class="group bg-gray-400 focus:bg-primary-300"
            v-if="year.previousYear !== 0"
            @click="pageHandler(year)"
            ><span class="text-white group-focus:text-primary-300"
              >{{ year.previousYear }} - {{ year.text }}</span
            >
          </base-button>
        </div>
      </div>
    </div>
    <!-- End Pagination -->
    <div class="flex justify-center" v-if="isloading">
      <base-spinner></base-spinner>
    </div>

    <table v-else style="width:100%">
      <thead>
        <tr>
          <th colspan="2" class="text-center ">
            <h4>วิชา</h4>
          </th>
          <th colspan="5" class="text-center ">
            <div class="flex justify-center">
              <h4 class="pr-2">หลักสูตร {{ minYear }}</h4>
              <!-- <svg
                class="w-6 h-6"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                ></path>
              </svg> -->
            </div>
          </th>
          <th colspan="6" class="text-center">
            <h4>หลักสูตร {{ maxYear }}</h4>
          </th>
        </tr>
        <tr class="text-center">
          <th>เงือนไข</th>
          <th>วิชาเอก</th>
          <th>Code</th>
          <th>ชื่อวิชา</th>
          <th>Type</th>
          <th>Unit</th>
          <th>คัดลอก</th>
          <!-- <th>เลือก</th> -->

          <th>Code</th>
          <th>ชื่อวิชา</th>
          <th>Type</th>
          <th>Unit</th>
          <th>ลบวิชา</th>
          <!-- <th>เลือก</th> -->
          <!-- <th></th> -->
        </tr>
      </thead>
      <div class="h-2"></div>

      <tbody>
        <subject-item
          :id="offLink"
          :key="offLink"
          :offLink="offLink"
          :years="selectedYears"
          :program="program"
          @selectSubject="selectSubject"
          @setNewSubject="setOffLinkList"
          @subjectStatus="updateSubjectStatus"
          :saveFlag="saveFlag"
          :refreshFlag="refreshFlag"
          v-for="offLink in offLinkList"
        ></subject-item>
      </tbody>
    </table>
    <div class="flex justify-center py-4">
      <base-button @click="addSubject">เพิ่มวิชา</base-button>
    </div>
  </div>
  <!-- {{ selectedSubject }}-->
  {{ JSON.stringify(subjectInState) }}

  <!-- <base-spinner></base-spinner> -->

  {{ subjectStatus }}

  <br />
  <!-- {{ testParent }}
  {{ testChild }} -->

  <scroll-to-top></scroll-to-top>
  <base-dialog
    title="บันทึกยืนยัน"
    :show="addCurDialog"
    @close="addCurDialog = false"
  >
    <p>หลักสูตรใหม่ถูกบันทึกเสร็จสิ้นแล้ว</p>
  </base-dialog>
  <base-dialog
    title="ยีนยันการลบ"
    :show="deleteCurDialog"
    @close="deleteCurDialog = false"
  >
    <p>ลบหลักสูตร {{ selectedDeleteCur }} เสร็จสิ้นแล้ว</p>
  </base-dialog>
  <spinner-dialog :show="loadingDialog"></spinner-dialog>

  <h2 class="text-red-500">{{ error }}</h2>
</template>

<script>
import SubjectItem from "../../components/subjects/SubjectItem.vue";
import ScrollToTop from "../../components/UI/ScrollToTop.vue";
import BaseSpinner from "../../components/UI/BaseSpinner.vue";
import SpinnerDialog from "../../components/UI/SpinnerDialog.vue";
import { getId } from "../../js/utility.js";
import LoadDataMixin from "../../mixins/loadData.js";

export default {
  mixins: [LoadDataMixin],
  components: {
    SubjectItem,
    ScrollToTop,
    BaseSpinner,
    SpinnerDialog,
  },
  data() {
    return {
      selectedCopyFormCur: null,
      selectedDeleteCur: null,
      inputCopyToCur: null,

      selectedYears: [],
      saveFlag: false,
      refreshFlag: false,
      selectedSubject: [],
      offLinkList: [],
      program: "ibmp",

      // component data

      showForm: null,

      loadingDialog: false,
      addCurDialog: false,
      deleteCurDialog: false,
      isloading: false,
      saveSubjectDialog: false,
      subjectStatus: [],
      error: null,

      page: 1,
    };
  },
  watch: {
    showForm(newValue) {
      if (newValue === "addCur") {
        this.selectedCopyFormCur = this.stateMaxYear;
      }
    },
    subjectStatus: {
      deep: true,
      handler() {
        if (this.isFormValid) {
          this.saveSubjectDialog = false;
        }
      },
    },
  },
  computed: {
    // testChild() {
    //   return this.$store.getters["subjects/hasChildSubject"]("s13");
    // },
    // testParent() {
    //   return this.$store.getters["subjects/hasParentSubject"]("s13");
    // },

    stateMaxYear() {
      return this.$store.getters["subjects/maxYear"];
    },
    years() {
      return this.$store.getters["subjects/years"];
    },
    maxYear() {
      return Math.max(...this.selectedYears);
    },
    minYear() {
      return Math.min(...this.selectedYears);
    },
    // subjects() {},
    subjectInState() {
      return this.$store.getters["subjects/subjects"];
    },
  },
  created() {
    this.loadData();
  },
  mounted() {},
  methods: {
    async loadData() {
      await this.loadYear();
      await this.loadSubject();
      this.setDefaultYears();
      this.setOffLinkList();
    },
    // loadYear, loadSubject in LoadDataMixin
    addSubject() {
      this.offLinkList.push(getId());
    },
    deleteSubjects() {
      this.$store.dispatch("subjects/deleteSubjects", {
        id: this.selectedSubject,
      });
      this.setOffLinkList();
    },
    setDefaultYears() {
      const year = this.years.find((e) => e.value === this.stateMaxYear);

      this.selectedYears[0] = year.previousYear;
      this.selectedYears[1] = year.value;
    },
    setOffLinkList() {
      console.log("start....set subject");
      console.log(this.selectedYears);

      const allOffLink = this.$store.getters["subjects/getOffLinkByYear"](
        this.selectedYears
      );

      console.log(allOffLink);

      this.offLinkList = allOffLink;

      // refresh data in SubjectItem
      this.refreshFlag = !this.refreshFlag;
    },
    updateSubjectStatus(payload) {
      const subject = payload.subject;
      const theSubject = this.subjectStatus.find((x) => x.subject === subject);
      const index = this.subjectStatus.indexOf(theSubject);

      console.log(payload);
      if (subject) {
        if (index > -1) {
          this.subjectStatus.splice(index, 1, payload);
        } else {
          if (payload) {
            this.subjectStatus.push(payload);
          }
        }
      }
    },
    selectSubject(payload) {
      const index = this.selectedSubject.indexOf(payload);
      if (index > -1) {
        this.selectedSubject.splice(index, 1);
      } else {
        if (payload) {
          this.selectedSubject.push(payload);
        }
      }
    },
    saveSubject() {
      // validation
      this.saveSubjectDialog = false;
      if (this.isFormValid()) {
        this.saveFlag = !this.saveFlag;
        alert("เริ่มต้นการบันทึกแล้ว");
      } else {
        this.saveSubjectDialog = true;
      }
    },
    isFormValid() {
      const inValidSubject = this.subjectStatus.some((e) => !e.status);
      return !inValidSubject;
    },
    pageHandler(year) {
      let yearArr = [];
      yearArr.push(year.value);
      yearArr.push(year.previousYear);
      this.selectedYears = yearArr;
      this.setOffLinkList();
    },
    async addCurriculum() {
      this.loadingDialog = true;
      try {
        await this.$store.dispatch("subjects/copyCurriculum", {
          copyFromCur: this.selectedCopyFormCur,
          copyToCur: this.inputCopyToCur,
          program: this.program,
        });
      } catch (e) {
        this.error = e.message;
      }

      this.setDefaultYears();
      this.setOffLinkList();
      this.loadingDialog = false;
      this.addCurDialog = true;
      this.showForm = null;
      // this.inputCopyToCur = "";
    },
    async deleteCurriculum() {
      const cf = confirm(
        "คุณต้องการลบข้อมูลทั้งหมดของปีการศึกษา " +
          this.selectedDeleteCur +
          " ของหลักสูตร " +
          this.program +
          " ใช่หรือไม่ ?"
      );
      if (!cf) {
        return;
      }
      this.loadingDialog = true;
      console.log("sending delete data...");
      try {
        await this.$store.dispatch("subjects/deleteSubjectByYear", {
          year: this.selectedDeleteCur,
          program: this.program,
        });
      } catch (e) {
        alert(e.message);
        this.error = e.message;
      }

      this.setDefaultYears();
      this.setOffLinkList();
      this.loadingDialog = false;
      this.deleteCurDialog = true;
      this.showForm = null;
    },
  },
};
</script>

