export default {
  sections(state) {
    return state.sections;
  },
  fullName(state, getters) {
    let secArr = [];
    const sections = getters.sections;
    for (const sec of sections) {
      secArr.push({
        ...sec,
        fullName: sec.code + " - " + sec.time,
      });
    }
    return secArr;
  },
};
