export default {
  methods: {
    async loadYear(forceUpdate = false) {
      this.isloading = true;
      try {
        console.log("try fetch data");
        await this.$store.dispatch("subjects/setYear", {
          forceUpdate: forceUpdate,
          program: this.program,
        });
      } catch (e) {
        console.log("error in loadYear");
        this.error = e.message;
      }

      this.isloading = false;
    },
    async loadSubject(forceUpdate = false) {
      this.isloading = true;
      try {
        console.log("try fetch data");
        await this.$store.dispatch("subjects/setSubject", {
          forceUpdate: forceUpdate,
          program: this.program,
        });
      } catch (e) {
        this.error = e.message;
      }

      this.isloading = false;
    },
  },
};
