export default {
  addQuota(state, subjectData) {
    state.subjects.push(subjectData);
  },
  setSubject(state, payload) {
    console.log(payload);
    state.subjects = payload;
  },
  addYear(state, payload) {
    state.years.push(payload);
  },
  setYear(state, payload) {
    console.log(payload);
    state.years = payload;
  },
};
