<template>
  <tr>
    <td class="w-10 px-1">
      <input
        type="text"
        class="form-control"
        placeholder=""
        v-model="condition"
        @input="dataHasChaged = true"
      />
      <!-- {{ condition }} -->
    </td>
    <td class="w-20">
      <select
        class="form-control px-1"
        placeholder=""
        v-model="major"
        @input="dataHasChaged = true"
      >
        <option v-for="major in majors" :value="major.value" :key="major.value">
          {{ major.text }}
        </option>
      </select>
      <!-- {{ major }} -->
    </td>

    <!-- หลักสูตร 1  -->
    <td class="pr-2 w-20">
      <input
        type="text"
        class="form-control"
        :class="{ errors: !subject1IsValid }"
        placeholder="Code"
        v-model="subject1.code"
        @input="dataHasChaged = true"
      />
      <!-- {{ subject1.code }} -->
    </td>
    <td class="pr-2 w-52">
      <input
        type="text"
        class="form-control"
        :class="{ errors: !subject1IsValid }"
        placeholder="ชื่อวิชา"
        v-model="subject1.name"
        @input="dataHasChaged = true"
      />
      <!-- {{ subject1.name }} -->
    </td>
    <td class="pr-2 w-24">
      <div class="relative inline-block w-full text-gray-700">
        <select
          class="form-control"
          placeholder="Regular input"
          v-model="subject1.type"
          :class="{
            'disable-select': !subject1.type,
            errors: !subject1IsValid,
          }"
          @input="dataHasChaged = true"
        >
          <!-- :disabled="!subject1.type" -->
          <option value="0" disabled>โปรดเลือก</option>
          <option value="core">แกน</option>
          <option value="require">เอกบังคับ</option>
          <option value="elective">เอกเลือก</option>
        </select>
      </div>
      <!-- {{ subject1.type }} -->
    </td>
    <td class="pr-2 w-16">
      <input
        type="text"
        class="form-control"
        :class="{ errors: !subject1IsValid }"
        placeholder="Unit"
        v-model="subject1.unit"
        @input="dataHasChaged = true"
      />
      <!-- {{ subject1.unit }} -->
    </td>

    <!-- checkbox used for send data to parent and delete data in DB -->
    <!-- <td class="pr-2 w-1 text-center">
      <input
        type="checkbox"
        :value="subject1.id"
        @input="$emit('selectSubject', $event.target.value)"
      />
    </td> -->

    <td class="w-20 px-2 text-center">
      <base-button class="w-full h-10" @click="copySubject()">
        <div class="flex justify-center items-center">
          <svg
            class="w-6 h-6"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M13 5l7 7-7 7M5 5l7 7-7 7"
            ></path>
          </svg>
        </div>
      </base-button>
    </td>

    <!-- หลักสูตร 2  -->
    <td class="pr-2 w-20">
      <input
        type="text"
        class="form-control"
        :class="{ errors: !subject2IsValid }"
        placeholder="Code"
        v-model="subject2.code"
        @input="dataHasChaged = true"
      />
      <!-- {{ subject2.code }} -->
    </td>

    <td class="pr-2 w-52">
      <input
        type="text"
        class="form-control"
        :class="{ errors: !subject2IsValid }"
        placeholder="ชื่อวิชา"
        v-model="subject2.name"
        @input="dataHasChaged = true"
      />
      <!-- {{ subject2.name }} -->
    </td>
    <td class="pr-2 w-24">
      <div class="relative inline-block w-full text-gray-700">
        <select
          class="form-control"
          placeholder="Regular input"
          v-model="subject2.type"
          :class="{
            'disable-select': !subject2.type,
            errors: !subject2IsValid,
          }"
          @input="dataHasChaged = true"
        >
          <option value="0" disabled>โปรดเลือก</option>
          <option value="core">แกน</option>
          <option value="require">เอกบังคับ</option>
          <option value="elective">เอกเลือก</option>
        </select>
      </div>
      <!-- {{ subject2.type }} -->
    </td>
    <td class="pr-2 w-16">
      <input
        type="text"
        class="form-control"
        :class="{ errors: !subject2IsValid }"
        placeholder="Unit"
        v-model="subject2.unit"
        @input="dataHasChaged = true"
      />
      <!-- {{ subject2.unit }} -->
    </td>
    <td class="pr-2 w-4">
      <base-button mode="gray" class="w-full" @click="deleteSubject()"
        >ลบ</base-button
      >
    </td>

    <!-- checkbox used for send data to parent and delete data in DB -->
    <!-- <td class="pr-2 w-1 text-center">
      <input
        type="checkbox"
        :value="subject2.id"
        @input="$emit('selectSubject', $event.target.value)"
      />
    </td> -->
  </tr>
  <!-- {{ this.dataHasChaged }} -->
</template>

<script>
import BaseButton from "../UI/BaseButton.vue";
export default {
  components: { BaseButton },
  props: {
    id: {
      type: String,
      required: true,
    },
    offLink: {
      required: true,
    },
    years: {
      type: Array,
      required: true,
    },
    saveFlag: {
      type: Boolean,
      required: true,
    },
    refreshFlag: {
      type: Boolean,
      required: true,
    },
    program: {
      type: String,
      required: true,
    },
  },
  emits: ["selectSubject", "setNewSubject", "subjectStatus"],
  created() {
    this.setSubjectData();
  },
  mounted() {
    this.settingValue = false;
  },

  data() {
    return {
      subject1: {
        id: null,
        code: null,
        name: null,
        type: null,
        unit: null,
        year: null,
        offLink: null,
      },
      subject1IsValid: true,

      subject2: {
        id: null,
        code: null,
        name: null,
        type: null,
        unit: null,
        year: null,
        offLink: null,
      },
      subject2IsValid: true,
      condition: null,
      major: null,

      // component data
      settingValue: true,
      subjectData: null,
      dataHasChaged: false,
    };
  },
  computed: {
    majors() {
      return this.$store.getters["subjects/majors"];
    },
    maxYear() {
      return Math.max(...this.years);
    },
    minYear() {
      return Math.min(...this.years);
    },
  },
  watch: {
    subject1: {
      deep: true,
      handler() {
        console.log("subject1 update");

        if (!this.subject1.type) {
          this.subject1.type = 0;
        }

        // form validation
        this.subject1IsValid = true;
        this.subject2IsValid = true;
        this.isFormValid();
      },
    },
    subject2: {
      deep: true,
      handler() {
        console.log("subject2 update");

        if (!this.subject2.type) {
          this.subject2.type = 0;
        }
        // form validation
        this.subject1IsValid = true;
        this.subject2IsValid = true;
        this.isFormValid();
      },
    },
    condition() {
      console.log("condition update");
      // form validation
      this.subject1IsValid = true;
      this.subject2IsValid = true;
      this.isFormValid();
    },
    major() {
      console.log("major update");

      // form validation
      this.subject1IsValid = true;
      this.subject2IsValid = true;
      this.isFormValid();
    },
    async saveFlag(newValue, oldValue) {
      console.log(this.dataHasChaged);
      if (newValue !== oldValue) {
        if (this.dataHasChaged) {
          await this.updateSubject();
          console.log("saveFlag fire 2");
          this.setNewSubjectFromStore();
        }
      }
    },
    refreshFlag(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.setSubjectData();
      }
      this.dataHasChaged = false;
    },
    subject1IsValid(newValue) {
      console.log("subject1IsValid update");
      this.$emit("subjectStatus", {
        subject: this.subject1.offLink,
        status: newValue,
      });
    },
    subject2IsValid(newValue) {
      console.log("ssubject2IsValid update");
      this.$emit("subjectStatus", {
        subject: this.subject2.offLink,
        status: newValue,
      });
    },
  },

  methods: {
    clearValue() {
      this.setEmpty(this.subject2);
    },
    setNewSubjectFromStore() {
      this.$emit("setNewSubject");
    },
    copySubject() {
      // this.subject2.id = new Date().getTime().toString();
      this.subject2.code = this.subject1.code;
      this.subject2.name = this.subject1.name;
      this.subject2.type = this.subject1.type;
      this.subject2.unit = this.subject1.unit;
      this.subject2.offLink = this.subject1.offLink;
      this.dataHasChaged = true;
    },
    setSubjectData() {
      // this.offLinkData = JSON.parse(JSON.stringify(this.offLink));

      const subject1 = this.$store.getters[
        "subjects/getSubjectByYearAndOffLink"
      ]({ year: this.minYear, offLink: this.offLink });

      if (subject1) {
        const subject1Data = JSON.parse(JSON.stringify(subject1));
        this.subject1 = subject1Data;
        this.condition = subject1Data.condition;
        this.major = subject1Data.majorId;
      } else {
        this.setEmpty(this.subject1);
        this.subject1.offLink = this.offLink;
      }

      const subject2 = this.$store.getters[
        "subjects/getSubjectByYearAndOffLink"
      ]({ year: this.maxYear, offLink: this.offLink });

      if (subject2) {
        const subject2Data = JSON.parse(JSON.stringify(subject2));
        this.subject2 = subject2Data;
        this.condition = subject2Data.condition;
        this.major = subject2Data.majorId;
      } else {
        this.setEmpty(this.subject2);
        this.subject2.offLink = this.offLink;
      }

      // this.setEmpty(this.subject2);
    },
    setEmpty(subjectForm) {
      subjectForm.id = null;
      subjectForm.code = null;
      subjectForm.name = null;
      subjectForm.type = null;
      subjectForm.unit = null;
      subjectForm.year = null;
      subjectForm.offLink = null;
    },
    async updateSubject() {
      console.log(
        "updateSubject start " + this.subject1.code + this.subject2.code
      );
      if (this.subject1IsValid) {
        const submittedData1 = {
          id: this.subject1.id,
          majorId: this.major,
          type: this.subject1.type,
          name: this.subject1.name,
          code: this.subject1.code,
          unit: this.subject1.unit,
          year: this.minYear,
          offLink: this.subject1.offLink,
          condition: this.condition,
          program: this.program,
        };

        if (this.subject1.code) {
          try {
            await this.$store.dispatch(
              "subjects/updateSubject",
              submittedData1
            );
          } catch (e) {
            this.error = e.message;
          }
        }
      }

      if (this.subject2IsValid) {
        const submittedData2 = {
          id: this.subject2.id,
          majorId: this.major,
          type: this.subject2.type,
          name: this.subject2.name,
          code: this.subject2.code,
          unit: this.subject2.unit,
          year: this.maxYear,
          offLink: this.subject2.offLink,
          condition: this.condition,
          program: this.program,
        };
        console.log(submittedData2);
        if (this.subject2.code) {
          try {
            await this.$store.dispatch(
              "subjects/updateSubject",
              submittedData2
            );
          } catch (e) {
            this.error = e.message;
          }
        }
      }

      // if send data success
      this.dataHasChaged = false;
    },
    isFormValid() {
      if (
        this.subject1.code &&
        this.subject1.name &&
        this.subject1.unit &&
        this.subject1.type
      ) {
        this.subject1IsValid = true;
      } else {
        if (
          !this.subject1.code &&
          !this.subject1.name &&
          !this.subject1.unit &&
          !this.subject1.type
        ) {
          this.subject1IsValid = true;
        } else {
          this.subject1IsValid = false;
        }
      }

      if (
        this.subject2.code &&
        this.subject2.name &&
        this.subject2.unit &&
        this.subject2.type
      ) {
        this.subject2IsValid = true;
      } else {
        if (
          !this.subject2.code &&
          !this.subject2.name &&
          !this.subject2.unit &&
          !this.subject2.type
        ) {
          this.subject2IsValid = true;
        } else {
          this.subject2IsValid = false;
        }
      }
    },

    async deleteSubject() {
      if (this.subject2.id) {
        const cf = confirm(
          "คุณต้องการลบวิชา " +
            this.subject2.code +
            " ในปีการศึกษา " +
            this.maxYear +
            " ใช่หรือไม่ ?"
        );
        if (cf) {
          try {
            await this.$store.dispatch("subjects/deleteSubject", {
              id: this.subject2.id,
              program: this.program,
            });
          } catch (e) {
            this.error = e.message;
          }
          this.setNewSubjectFromStore();
        }
      } else {
        alert("ลบไม่ได้เนื่องจากไม่มีวิชานี้อยู่ในระบบ");
      }
    },
  },
};
</script>

<style scoped>
.disable-select {
  opacity: 0.6;
}

.errors {
  @apply border-red-500;
}
</style>
