<template>
  <div class="py-10 text-center ">
    <h3>เพิ่มข้อมูลโควต้า</h3>
  </div>

  <table style="table-fixed">
    <thead>
      <tr class="text-center">
        <th class="w-1/12">รหัสวิชา</th>
        <th class="w-0.5/12">หลักสูตร</th>
        <th class="w-3/12">ชื่อวิชา</th>
        <th class="w-2/12">Sec</th>
        <th class="w-0.5/12">Gr</th>
        <th class="w-0.5/12">Cam</th>
        <th class="w-0.5/12">จำนวนรับ</th>
        <th class="w-2/12">อาจารย์</th>
        <th class="w-0.5/12">เงื่อนไข</th>
        <!-- <th class="w-0.5/12">ห้องเรียน</th>
        <th class="w-0.5/12">Midterm</th>
        <th class="w-0.5/12">Final</th> -->
      </tr>
    </thead>
    <div class="h-2"></div>

    <tbody>
      <add-quota-item
        v-for="formId in formIdArr"
        :ref="formId"
        :key="formId"
        :id="formId"
      ></add-quota-item>
    </tbody>
  </table>
  <div class="px-2 py-2">จำนวน {{ formIdArr.length }} แถว</div>

  <div>
    <base-card>
      <div class="flex justify-center py-4">
        <base-button
          mode="flat"
          @click="addRowVisible = !addRowVisible"
          class=" px-8"
          >+ เพิ่มแถว</base-button
        >
        <base-button @click="submitData()" class="bg-primary-300 px-8"
          >บันทึกข้อมูล</base-button
        >
      </div>
      <div v-if="addRowVisible" class="flex justify-center">
        <div class="max-w-lg ">
          <form @submit.prevent="addRow()">
            <label>จำนวนแถวที่ต้องการ</label>
            <input type="text" class="form-control" v-model.trim="addRowNo" />
            <div class="flex justify-center">
              <base-button mode="outline px-10 mt-2">เพิ่ม</base-button>
            </div>
          </form>
        </div>
      </div>
    </base-card>
  </div>
  <!-- <div class="flex justify-center py-4">
    <base-button @click="null" class="bg-primary-300 px-8"
      >บันทึกข้อมูล</base-button
    >
  </div> -->
  <div v-if="logVisible">
    {{ userProgram }}
    <hr />
    {{ formIdArr }}
    <hr />
    <h4 class="text-red-500 text-center" v-if="!!error">{{ error }}</h4>
    <hr />
    {{ quota }}
    <hr />
    <hr />
    <button @click="test()">TEST</button>
  </div>
  <toast
    :show="!formValid"
    mode="error"
    @close="closeError"
    :duration="5000"
    message="ข้อมูลโควต้าไม่ครบถ้วน โปรดตรวจสอบและลองใหม่อีกครั้ง"
  >
  </toast>
</template>

<script>
import AddQuotaItem from "../../components/Quota/AddQuotaItem.vue";
import { getId } from "../../js/utility.js";
import { isLogVisible } from "../../js/debug.js";
import LoadDataMixin from "../../mixins/loadData.js";

export default {
  mixins: [LoadDataMixin],
  components: { "add-quota-item": AddQuotaItem },
  data() {
    return {
      formIdArr: [],
      formValid: true,

      addRowNo: 1,
      addRowVisible: false,
      defaultFormNo: 1,
      error: null,
      program: "ibmp",
    };
  },
  computed: {
    // logVisible() {
    //   return process.env.VUE_APP_SHOW_LOGS === "1";
    // },
    logVisible() {
      return isLogVisible();
    },
    quota() {
      return this.$store.getters["quota/quota"];
    },
    userProgram() {
      return this.$store.getters["userProgram"];
    },
  },
  watch: {},
  created() {
    this.addRow();
    this.loadData();
  },
  methods: {
    test() {
      for (const formId of this.formIdArr) {
        const x = "test";
        this.$refs[formId][x]();
      }
    },
    callChildMethod(methodName) {
      for (const formId of this.formIdArr) {
        this.$refs[formId][methodName]();
      }
    },
    async loadData() {
      await this.loadYear();
      await this.loadSubject();
    },
    addRow() {
      for (let i = 0; i < this.addRowNo; i++) {
        this.formIdArr.push(getId());
      }
    },
    submitData() {
      // เช็ค input ทั้งหมด ว่ามีปัญหาไหม
      this.formValid = true;
      this.isFormValid();
      if (!this.formValid) {
        return;
      }
      this.callChildMethod("submitData");
      this.$router.replace({
        name: "quota-list",
        params: { success: true },
      });
    },

    isFormValid() {
      // callChildMethod
      this.callChildMethod("isFormValid");
      for (const formId of this.formIdArr) {
        this.$refs[formId].formValid;
      }
      const inValidData = this.formIdArr.some(
        (formId) => this.$refs[formId].formValid === false
      );
      this.formValid = !inValidData;
    },
    closeError() {
      this.formValid = true;
    },
  },
};
</script>
