<template>
  <tr>
    <td class=" px-1">
      <span v-if="logVisible">
        {{ formValid }}
      </span>
      <!-- <input type="text" class="form-control" placeholder="" value="AC201" /> -->
      <serach-input
        :options="subjects"
        placeHolder="code"
        :setValue="null"
        @selectedValue="selectSubjectCode"
        textField="code"
        valueField="code"
        :error="!subjectCode.isValid"
      ></serach-input>
    </td>
    <td class=" px-1">
      <serach-input
        :options="FilteredSubjectByCode"
        placeHolder=""
        :setValue="null"
        @selectedValue="selectYear"
        textField="year"
        valueField="year"
        :error="!year.isValid"
      ></serach-input>
    </td>
    <td class=" px-1 pt-1">
      <input
        type="text"
        class="form-control "
        placeholder=""
        v-model="subName"
        disabled
      />
    </td>
    <td class=" px-1">
      <serach-input
        :options="sections"
        placeHolder=""
        :setValue="null"
        @selectedValue="selectSec"
        textField="fullName"
        valueField="code"
        :error="!sec.isValid"
      ></serach-input>
    </td>
    <td class="px-1 pt-1">
      <input
        type="text"
        list="Gr"
        class="form-control"
        :class="{
          'bg-red-50': !group.isValid,
          'border-red-400': !group.isValid,
        }"
        placeholder=""
        v-model="group.val"
      />
      <datalist id="Gr">
        <option value="01">01</option>
        <option value="02">02</option>
        <option value="03">03</option>
        <option value="04">04</option>
        <option value="05">05</option>
        <option value="06">06</option>
        <option value="07">07</option>
        <option value="08">08</option>
        <option value="09">09</option>
        <option value="10">10</option>
      </datalist>
    </td>
    <td class=" px-1 pt-1">
      <input
        type="text"
        list="Cam"
        class="form-control"
        placeholder=""
        v-model="campus.val"
        :class="{
          'bg-red-50': !campus.isValid,
          'border-red-400': !campus.isValid,
        }"
      />
      <datalist id="Cam">
        <option value="R">R</option>
        <option value="T">T</option>
      </datalist>
    </td>
    <td class="px-1 pt-1">
      <input
        type="text"
        class="form-control"
        placeholder=""
        v-model="quota.val"
        :class="{
          'bg-red-50': !quota.isValid,
          'border-red-400': !quota.isValid,
        }"
      />
    </td>
    <td class=" px-1 pb-0.5">
      <serach-input
        :options="instructors"
        placeHolder=""
        :setValue="null"
        @selectedValue="selectInst"
        textField="fullName"
        valueField="id"
        :error="!instructor.isValid"
      ></serach-input>
    </td>
    <td class=" px-1">
      <input
        type="text"
        class="form-control"
        placeholder=""
        v-model="condition.val"
        :class="{
          'bg-red-50': !condition.isValid,
          'border-red-400': !condition.isValid,
        }"
      />
    </td>
    <!-- <td class=" px-1">
      <input type="text" class="form-control" placeholder="" value="not use" />
    </td>
    <td class=" px-1">
      <input type="text" class="form-control" placeholder="" value="not use" />
    </td>
    <td class=" px-1">
      <input type="text" class="form-control" placeholder="" value="not use" />
    </td> -->
  </tr>
  <tr v-if="logVisible">
    <td>{{ subjectCode.val }} | {{ subjectCode.isValid }}</td>
    <td>{{ year.val }} | {{ year.isValid }}</td>
    <td>{{ subName }}</td>
    <td>{{ sec.val }} | {{ sec.isValid }}</td>
    <td>{{ group.val }} | {{ group.isValid }}</td>
    <td>{{ campus.val }} | {{ campus.isValid }}</td>
    <td>{{ quota.val }} | {{ quota.isValid }}</td>
    <td>{{ instructor.val }} | {{ instructor.isValid }}</td>
    <td>{{ condition.val }} | {{ condition.isValid }}</td>
    <td></td>
  </tr>
</template>

<script>
import SerachInput from "../../components/UI/SearchInput.vue";
import { isLogVisible } from "../../js/debug.js";
export default {
  data() {
    return {
      FilteredSubjectByCode: [],
      formValid: true,
      loading: null,

      // system
      subOffLink: null,
      subName: null,
      subId: null,

      // user
      subjectCode: {
        val: null,
        isValid: true,
      },
      year: {
        val: null,
        isValid: true,
      },

      sec: {
        val: null,
        isValid: true,
      },
      group: {
        val: null,
        isValid: true,
      },
      campus: {
        val: null,
        isValid: true,
      },
      quota: {
        val: null,
        isValid: true,
      },
      instructor: {
        val: null,
        isValid: true,
      },
      condition: {
        val: null,
        isValid: true,
      },
      // selectedRoom: null,
      // selectedMidTerm: null,
      // selectedFinal: null,
    };
  },
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  components: {
    "serach-input": SerachInput,
  },
  emits: ["quotaStatus"],
  created() {},
  watch: {
    subjectCode: {
      deep: true,
      handler() {
        this.updateYearData();
        this.updateName();
      },
    },
    year: {
      deep: true,
      handler() {
        this.updateName();
      },
    },
  },

  computed: {
    logVisible() {
      return isLogVisible();
    },
    subjects() {
      return this.$store.getters["subjects/subjects"];
    },
    sections() {
      return this.$store.getters["sections/fullName"];
    },
    instructors() {
      return this.$store.getters["instructors/fullName"];
    },
  },
  methods: {
    test() {
      alert("working.." + this.id);
    },
    async submitData() {
      console.log("starting submitData in AddQuotaItem");

      const submittedData = {
        subId: this.subId,
        offLink: this.subOffLink,
        secId: this.sec.val,
        group: this.group.val,
        campus: this.campus.val,
        quota: this.quota.val,
        instId: this.instructor.val,
      };

      console.log("send data to vuex");
      console.log(submittedData);
      this.$store.dispatch("quota/addQuota", submittedData);
      // await new Promise((r) => setTimeout(r, 2000));
      this.$emit("quotaStatus", {
        id: this.id,
        valid: this.formValid,
        loading: "completed",
      });
      // this.$router.replace("/quota");
    },
    isFormValid() {
      this.formValid = true;
      this.subjectCode.isValid = true;
      this.year.isValid = true;
      this.sec.isValid = true;
      this.group.isValid = true;
      this.campus.isValid = true;
      this.quota.isValid = true;
      this.instructor.isValid = true;
      this.condition.isValid = true;

      if (!this.subjectCode.val) {
        this.formValid = false;
        this.subjectCode.isValid = false;
      }
      if (!this.year.val) {
        this.formValid = false;
        this.year.isValid = false;
      }
      if (!this.sec.val) {
        this.formValid = false;
        this.sec.isValid = false;
      }

      if (!this.group.val) {
        this.formValid = false;
        this.group.isValid = false;
      }
      if (!this.campus.val) {
        this.formValid = false;
        this.campus.isValid = false;
      }
      if (!this.quota.val) {
        this.formValid = false;
        this.quota.isValid = false;
      }

      if (!this.instructor.val) {
        this.formValid = false;
        this.instructor.isValid = false;
      }
      if (!this.condition.val) {
        this.formValid = false;
        this.condition.isValid = false;
      }
    },
    updateYearData() {
      this.FilteredSubjectByCode = this.$store.getters[
        "subjects/getSubjectByCode"
      ](this.subjectCode.val);
    },
    updateName() {
      const sub = this.$store.getters["subjects/getSubjectByCodeAndYear"]({
        code: this.subjectCode.val,
        year: this.year.val,
      });

      if (sub) {
        this.subName = sub.name;
        this.condition.val = sub.condition;
        this.subId = sub.id;
        this.subOffLink = sub.offLink;
      }
    },
    selectSubjectCode(val) {
      this.subjectCode.val = val;
    },
    selectYear(val) {
      this.year.val = val;
    },
    selectSec(val) {
      this.sec.val = val;
    },
    selectInst(val) {
      this.instructor.val = val;
    },
  },
};
</script>
