<script>
import QuotaItem from "../../components/Quota/QuotaItem.vue";
import LoadDataMixin from "../../mixins/loadData.js";
import { isLogVisible } from "../../js/debug.js";
export default {
  mixins: [LoadDataMixin], // loadYear(), loadSubject()
  components: { QuotaItem },
  data() {
    return { isloading: null, yearNo: 4, offLinkList: [] };
  },
  async created() {
    await this.loadYear();
    await this.loadSubject();
    await this.loadQuota();
    this.setOffLinkList();
  },
  computed: {
    // get OffLink
    subjects() {
      return this.$store.getters["subjects/subjects"];
    },
    quota() {
      return this.$store.getters["quota/quota"];
    },
    getMaxYears() {
      return this.$store.getters["subjects/getMaxYears"](this.yearNo);
    },
    years() {
      return this.$store.getters["subjects/years"];
    },
    logVisible() {
      return isLogVisible();
    },
  },
  methods: {
    async loadQuota(forceUpdate = false) {
      this.isloading = true;
      try {
        console.log("try fetch data");
        await this.$store.dispatch("quota/setQuota", {
          forceUpdate: forceUpdate,
        });
      } catch (e) {
        console.log("error in loadQuota");
        this.error = e.message;
      }

      this.isloading = false;
    },
    setOffLinkList() {
      console.log(this.$store.getters["quota/offLinkList"]);
      this.offLinkList = this.$store.getters["quota/offLinkList"];
    },
  },
};
</script>

