function sortSubjectCode(a, b) {
  var nameA = a.code.toUpperCase();
  var nameB = b.code.toUpperCase();
  if (nameA < nameB) {
    return -1;
  }
  if (nameA > nameB) {
    return 1;
  }
  return 0;
}

function sortSubjectYear(a, b) {
  var yearA = a.year;
  var yearB = b.year;
  if (yearA < yearB) {
    return -1;
  }
  if (yearA > yearB) {
    return 1;
  }
  return 0;
}

function sortYear(a, b) {
  var nameA = a.value;
  var nameB = b.value;
  if (nameA < nameB) {
    return -1;
  }
  if (nameA > nameB) {
    return 1;
  }
  return 0;
}

export default {
  subjects(state) {
    // return state.subjects;
    return state.subjects.sort(sortSubjectCode);
  },
  majors(state) {
    return state.majors;
  },
  years(state) {
    // return state.years;
    return state.years.sort(sortYear);
  },
  // hasParentSubject: (state, getters) => (id) => {
  //   const subject = getters.getSubjectsById(id);

  //   if (subject.preSubId) {
  //     const parentSubject = getters.getSubjectsById(subject.preSubId);
  //     return { value: true, parent: parentSubject };
  //   } else {
  //     return { value: false, parent: null };
  //   }
  // },
  // hasChildSubject: (state, getters) => (id) => {
  //   const childSubject = getters.getSubjectsByPreSubId(id);

  //   if (childSubject) {
  //     return { value: true, child: childSubject };
  //   } else {
  //     return { value: false, child: null };
  //   }
  // },
  maxYear(state, getters) {
    const years = getters.years;
    const yearMap = years.map((year) => year.value);
    // const maxYear = Math.max(...yearMap);
    // const selectedYear = years.find((year) => year.value === maxYear);
    return Math.max(...yearMap);
  },
  getMaxYears: (state, getters) => (yearNo) => {
    const years = getters.years;
    return years.slice(Math.max(years.length - yearNo, 0));
  },
  hasSubjectById: (state, getters) => (id) => {
    const subjects = getters.subjects;
    const findedSubject = subjects.find((subject) => subject.id === id);

    return !!findedSubject;
  },
  getYearByYear: (state, getters) => (year) => {
    const years = getters.years;
    const selectedYear = years.find(
      (theyear) => theyear.value === parseInt(year)
    );
    return selectedYear;
  },

  getSubjectByCode: (state, getters) => (subCode) => {
    const subjects = getters.subjects;
    const filteredSubjects = subjects.filter(
      (subject) => subject.code === subCode
    );
    filteredSubjects.sort(sortSubjectCode);
    return filteredSubjects;
  },

  getSubjectByCodeAndYear: (state, getters) => (payload) => {
    const subjectCode = payload.code;
    const subjectYear = payload.year;
    const subjects = getters.subjects;
    return subjects.find(
      (subject) => subject.code === subjectCode && subject.year === subjectYear
    );
  },

  getSubjectsById: (state, getters) => (id) => {
    const subjects = getters.subjects;
    return subjects.find((subject) => subject.id === id);
  },

  getSubjectsByOffLink: (state, getters) => (offLink) => {
    const subjects = getters.subjects;
    return subjects.filter((subject) => subject.offLink === offLink);
  },

  getSubjectByYear: (state, getters) => (years) => {
    const subjects = getters.subjects;
    const filteredSubjects = subjects.filter((subject) => {
      return years.some((year) => year === subject.year);
    });
    filteredSubjects.sort(sortSubjectCode);
    return filteredSubjects;
  },
  getLastSubjectByOffLink: (state, getters) => (OffLink) => {
    console.log("getLastSubjectByOffLink");
    console.log(OffLink);
    const subjects = getters.getSubjectsByOffLink(OffLink);
    const sortedSub = subjects.sort(sortSubjectYear);
    console.log(sortedSub[sortedSub.length - 1]);
    return sortedSub[sortedSub.length - 1];
  },
  getOffLinkByYear: (state, getters) => (years) => {
    const subjects = getters.subjects;
    const OffLinkSet = new Set();

    for (const subject of subjects) {
      const hasSubject = years.some((year) => year === subject.year);

      if (hasSubject) {
        OffLinkSet.add(subject.offLink);
      }
    }

    return [...OffLinkSet];
  },
  getSubjectByYearAndOffLink: (state, getters) => (payload) => {
    const year = payload.year;
    const offLink = payload.offLink;
    const subjects = getters.subjects;

    const foundSubject = subjects.find(
      (e) => e.year === year && e.offLink === offLink
    );

    return foundSubject;
  },
};
