<template>
  <teleport to="body">
    <div v-if="show" @click="tryClose" class="backdrop"></div>
    <dialog open v-if="show" class="font-body flex justify-center items-center">
      <div><base-spinner></base-spinner></div>
      <div>
        <p class="text-2xl text-white pl-4">กำลังดำเนินการ...</p>
      </div>
    </dialog>
  </teleport>
</template>

<script>
import BaseSpinner from "./BaseSpinner.vue";
export default {
  components: { "base-spinner": BaseSpinner },
  props: {
    show: {
      type: Boolean,
      required: true,
    },
    title: {
      type: String,
      required: false,
    },
    fixed: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  emits: ["close"],
  methods: {
    tryClose() {
      if (this.fixed) {
        return;
      }
      this.$emit("close");
    },
  },
};
</script>

