  async addYear(context, payload) {
    console.log("add year called");
    const program = payload.program;

    const prevYear = JSON.parse(JSON.stringify(context.getters.maxYear));
    console.log(prevYear);
    const YearData = {
      text: payload.year.toString(),
      value: payload.year,
      previousYear: prevYear,
    };

    const response = await fetch(
      `${process.env.VUE_APP_DB_URL}/years/${program}.json`,
      {
        method: "POST",
        body: JSON.stringify(YearData),
      }
    );

    const responseData = await response.json();
    console.log(responseData);

    if (!response.ok) {
      alert("can't send data to server,please try agian later");
    }

    context.commit("addYear", {
      text: payload.year.toString(),
      value: payload.year,
      previousYear: prevYear,
    });
  },
  async updateSubject(context, payload) {
    let subjectId = payload.id;
    const program = payload.program;
    const subjectData = {
      majorId: payload.majorId,
      type: payload.type,
      name: payload.name,
      code: payload.code,
      unit: payload.unit,
      year: payload.year,
      offLink: payload.offLink,
      condition: payload.condition,
    };
    console.log(subjectId);

    if (subjectId) {
      const response = await fetch(
        `${process.env.VUE_APP_DB_URL}/subjects/${program}/${subjectId}.json`,
        {
          method: "PUT",
          body: JSON.stringify(subjectData),
        }
      );

      console.log("PUT " + subjectData.code + "data to firebase");
      if (!response.ok) {
        alert("can't send data to server,please try agian later");
      }
    } else {
      const response = await fetch(
        `${process.env.VUE_APP_DB_URL}/subjects/${program}.json`,
        {
          method: "POST",
          body: JSON.stringify(subjectData),
        }
      );

      console.log("POST " + subjectData.code + "data to firebase");

      const responseData = await response.json();
      subjectId = responseData.name;

      if (!response.ok) {
        alert("can't send data to server,please try agian later");
      }
    }

    if (context.getters.hasSubjectById(subjectId)) {
      // if subject id is alreay exist update old data
      // update
      let subjects = context.getters.subjects;
      const subject = context.getters.getSubjectsById(subjectId);

      var index = subjects.indexOf(subject);
      console.log(index);

      if (index !== -1) {
        const newSubject = {
          ...subjectData,
          id: subjectId,
        };
        subjects.splice(index, 1, newSubject);
        console.log("removed ID " + payload.code);
      }

      console.log("update completed" + payload.code);
    } else {
      // else create ones

      context.commit("addSubject", {
        ...subjectData,
        id: subjectId,
      });
      console.log("save completed" + payload.code);
    }
  },
  async copyCurriculum(context, payload) {
    const program = payload.program;
    const copyFromCur = parseInt(payload.copyFromCur);
    const copyToCur = parseInt(payload.copyToCur);
    const subjects = context.getters.getSubjectByYear([copyFromCur]);

    const cloneSubjects = JSON.parse(JSON.stringify(subjects));

    for (const index in cloneSubjects) {
      delete cloneSubjects[index].id;
      cloneSubjects[index].year = copyToCur;

      const response = await fetch(
        `${process.env.VUE_APP_DB_URL}/subjects/${program}.json`,
        {
          method: "POST",
          body: JSON.stringify(cloneSubjects[index]),
        }
      );

      const responseData = await response.json();
      cloneSubjects[index].id = responseData.name;

      if (!response.ok) {
        alert("can't send data to server,please try agian later");
      }
    }
    // await context.dispatch.addYear({ year: copyToCur, program: program });
    await context.dispatch("addYear", { year: copyToCur, program: program });

    // context.commit("addYear", {
    //   text: copyToCur.toString(),
    //   value: copyToCur,
    //   previousYear: JSON.parse(JSON.stringify(context.getters.maxYear.value)),
    // });

    context.state.subjects.push(...cloneSubjects);
  },

