  async setSubject(context) {
    console.log("try set subejct in action.js");
    // if (!context.getters.shouldUpdate && !payload.forceUpdate) {
    //   return;
    // }
    const selectedProgram = context.rootGetters.userProgram;
    const response = await fetch(
      `${process.env.VUE_APP_DB_URL}/subjects/${selectedProgram}.json`
    );
    console.log(response);
    if (!response.ok) {
      const error = Error(response.message || "Can't get data from server");
      console.log(error);
      throw error;
    }

    const subjectData = await response.json();

    const subjects = [];
    for (const key in subjectData) {
      const coach = {
        id: key,
        majorId: subjectData[key].majorId,
        type: subjectData[key].type,
        name: subjectData[key].name,
        code: subjectData[key].code,
        unit: subjectData[key].unit,
        year: subjectData[key].year,
        offLink: subjectData[key].offLink,
        condition: subjectData[key].condition,
      };
      subjects.push(coach);
    }

    context.commit("setSubject", subjects);
    // context.commit("setFetchTime");
  },

  async setYear(context) {
    // if (!context.getters.shouldUpdate && !payload.forceUpdate) {
    //   return;
    // }
    console.log("setYear in vuex start");

    const selectedProgram = context.rootGetters.userProgram;
    console.log(selectedProgram);
    const response = await fetch(
      `${process.env.VUE_APP_DB_URL}/years/${selectedProgram}.json`
    );
    if (!response.ok) {
      console.log("error at setYear in action.js");
      const error = Error(response.message || "Can't get data from server");

      throw error;
    }

    const yearData = await response.json();

    const years = [];
    for (const key in yearData) {
      const year = {
        id: key,
        text: yearData[key].text,
        value: yearData[key].value,
        previousYear: yearData[key].previousYear,
      };
      years.push(year);
    }

    console.log(years);

    context.commit("setYear", years);
    // context.commit("setFetchTime");
  },
